.App-header {
	background: #333;
	color: #fff;
	padding: 10px 20px;
}

.App-subheader {
	background: darkorange;
	color: white;
	padding: 10px 20px;
}

.App-content {
	color: #333;
	padding: 10px 20px;
}

.App-emoji-button {
	-ms-flex-align: center;
	    align-items: center;
	background: transparent;
	border: 1px solid #000;
	border-radius: 50%;
	cursor: pointer;
	display: -ms-flexbox;
	display: flex;
	font-size: 30px;
	height: 50px;
	-ms-flex-pack: center;
	    justify-content: center;
	padding: 0;
	width: 50px;
}

.App-emoji-button:focus {
	-webkit-box-shadow: 0 0 0 4px rgba(189,8,22,.5);
	        box-shadow: 0 0 0 4px rgba(189,8,22,.5);
	outline: 0;
}



